.categories-list {
  letter-spacing: -.02em;
  color: rgba(17, 17, 17, .6);
  width: 100%;
  max-height: 228px;
  margin-top: 40px;
  margin-bottom: 40px;
  font-family: DM Sans Regular;
  line-height: 1.12;
  overflow-y: scroll;
}

@media (min-width: 768px) {
  .categories-list {
    width: 309px;
    max-height: 472px;
    font-size: 18px;
    display: inline-block;
  }

  ::-webkit-scrollbar-thumb {
