}

.categories-list__item.active {
  color: #4f2ee8;
  text-transform: uppercase;
  font-family: DM Sans Bold;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  height: 114px;
  background: #e0e0e0;
  border-radius: 12px;
}

